.footer {
    background-color: #222222;
    color: #ffffff;
    padding: 50px 0;
    display: flex; 
    justify-content: space-between; 
    align-items: flex-start;
    padding: 1%;
  }
  
  .footer__contact,
  .footer__recentposts,
  .footer__newsletter {
    flex: 1;
    margin-right: 20px; 
  }
  
  .footer__contact p {
    margin-bottom: 10px;
  }
  
  
  
  .footer__recentposts a {
    color: inherit;
    text-decoration: none; 
  }
  
  .footer__recentposts a:hover {
    color: #cccccc; 
  }
  
  .footer__newsletter .form {
    display: flex;
    align-items: center;
  }
  
  /* .footer__newsletter input[type="email"] {
    padding: 10px;
    border: 1px solid #cccccc;
    border-radius: 5px;
    margin-right: 10px;
    margin-top: 1%;
    flex: 1; 
  } */
  
  .footer__newsletter button {
    padding: 10px 15px;
    margin-top: 1%;
    width: 80%;
    background-color: #cccccc;
    color: #222222;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  .footer__newsletter button:hover {
    background-color: #968b8b;
  } 

  @media (max-width: 543px) {
    .footer {
        display: block;
    }
    .footer h3{
        text-align: center;
        margin-top: 2%;
        margin-right: 5%;
    }
    .footer__contact h3 {
      margin-right: 20%;
    }    
    .footer__contact,
    .footer__recentposts,
    .footer__newsletter {
        padding-left: 15%;
      width: 100%;
      display: inline-block;
    }
}

