body{
    background-color: #aeebaa;
    /* position: relative; */
    /* display: flex; */
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    /* min-height: 150dvh; */
    /* background: #f0f2f5; */
    /* background-image: url("images/bg.jpeg"); */
    /* background-size: cover; */
    background-position: center;
    /* position: inherit; */
  }
  .main {
    position: relative;
    width: 100%;
    display: block;
  }
  .logOut {
    /* display: inline-block; */
    /* float: left; */
    position: relative;
    align-items: end;
    justify-content: end;
    width: 100%;
    /* background-color: green; */
    margin-bottom: 1%;
  }
  .logOutButton {
    width: 20%;
    position: relative;
    left: 80%;    
    text-align: center;
    background-color: #e74c3c; 
    color: #fff;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.2s ease-in-out; 
  }
  .logOutButton:hover {
    background-color: #c0392b; 
  }

  .allDivs {
    /* display: inline-block; */
    min-height: 500px;
    width: 50%;
    /* min-height: 50%; */
    height:auto;
    border-radius: 10px;
    text-align: center;
    padding: 1%;
    background-color: rgb(228, 235, 235);
    position: relative;
    top: 15%;
    left: 25%;
    box-shadow: -2px -2px  rgb(182, 30, 30);
  }
  @media screen and (max-width: 500px) {
    .allDivs {
        width: 90%;
        left: 5%;
    }
  }
  .h3 {
    text-align: center;
    text-shadow: -2px -2px green;
    /* texts */
    font-size: 1.6em;
    margin-top: 7%;
    margin-bottom: 2%;
    padding-bottom: 0;
  }
  
  .inputTextArea {
    width: 50%;
    /* text-align: center; */
    padding-left: 20px;
    height: 32px;
    border-radius: 0 25px 0 25px;
    text-align: left;
    border: none;
  }
  .inputTextArea:focus{
    /* border: none; */
    color: rgb(188, 49, 49);
  }
  .addButton {
    width: 20%;
    margin-left: 0%;
    height: 35.8px;
    border-radius: 0 25px 0 25px;
    background-color: rgb(255, 123, 0);
    border: none;
    color: white;
  }
  .addButton:hover {
    cursor: pointer;
    background-color: rgb(235, 112, 4);
  }
  
  .mainDiv {
    padding-bottom: 4%;
    background-color: rgb(233, 230, 230);
  }
  
  .toDoDiv {
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    padding-top: 2%;
    height: auto;
  }
  
  .uniqToDoDiv{
    width: 100%;
    padding:1%;
    margin-bottom: 1%;
    background-color: rgb(196, 192, 192);
    border-radius: 5px;
    display: flex;
  }
  
  .checkbox {
    width: 10%;
    border-radius: 25px;
  }
  
  .textDiv {
    width: 80%;
    min-width: 200px;
    flex: 1;
    font-size: 1.2em;
    overflow-wrap: break-word;
  }
  .deleteButton {
    width: 10%;
    padding: 0;
    border: none;
    border-radius: 25px;
    background-color:  rgb(196, 192, 192);;
    color: white;
  }
  
  .deleteButton:hover {
    cursor: pointer;
    /* background-color:  rgba(240, 235, 235, 0.5); */
    color: red;
    transform: translateY(-1px);
  }
  
  .noTaskDiv{
    font-size: 1.4em;
    background-color: rgb(188, 49, 49);
  }
  
  .del {
    text-decoration: line-through;
  }