* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
}

.body {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #f0f2f5;
    background-image: url("images/bg.jpeg");
    background-size: cover;
    background-position: center;
    animation: changeBackground 11s infinite alternate;
}

@keyframes changeBackground {
    0% {
        background-image: url("images/bg2.jpg");
    }
    50% {
        background-image: url("images/bg.jpeg");
    }
    100% {
        background-image: url("images/bg3.jpg");
    }
}

.itemError {
    width: 100%;
    background-color: #db4437;
    color: white;
    padding: 10px;
    border-radius: 1;
    text-align: center;
    margin-bottom: 1%;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 350px;
    width: 100%;
    max-width: 400px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    background: #f6f3f3;
}

.sign-in-box {
    width: 100%;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
}

.form {
    display: flex;
    flex-direction: column;
}

.input-group {
    margin-bottom: 15px;
}

input::placeholder {
    color: rgba(48, 48, 48, 0.2);
}

label {
    margin-bottom: 5px;
    font-size: 14px;
    color: #555;
}

.input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    color: #333;
}

.input:focus {
    border-color: #007bff;
    outline: none;
}

.btn {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: #fff;
    background: #007bff;
    cursor: pointer;
    transition: background 0.3s;
    margin-bottom: 10px;
}

.btn:hover {
    background: #0056b3;
}

.social-login {
    text-align: center;
}

.social-login p {
    margin-bottom: 10px;
    color: #555;
}

.btn.google {
    background: #db4437;
}
.btn.google:hover {
    background: #d81c0b;
}


@media (max-width: 370px) {
    .container {
        padding: 15px;
        min-width: 300px;
    }

    h1 {
        font-size: 21px;
    }
    .btn {
        font-size: 14px;
        padding: 8px;
    }
}
